@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
:root {
  --up-indicator: hsl(163, 72%, 41%);
  --down-indicator: hsl(356, 69%, 56%);
  --facebook-color: hsl(208, 92%, 53%);
  --twitter-color: hsl(203, 89%, 53%);
  --instagram-start-color: hsl(329, 70%, 58%);
  --instagram-end-color: hsl(37, 97%, 70%);
  --youtube-color: hsl(348, 97%, 39%);
}

body.dark {
  --main-bg: hsl(230, 17%, 14%);
  --top-main-bg: hsl(232, 19%, 15%);
  --card-bg: hsl(228, 28%, 20%);
  --profile-text-color: hsl(228, 34%, 66%);
  --main-text: hsl(0, 0%, 100%);
  --toggle: linear-gradient(45deg, hsl(210, 78%, 56%), hsl(146, 68%, 55%));
}

body.light {
  --main-bg: hsl(0, 0%, 100%);
  --top-main-bg: hsl(225, 100%, 98%);
  --card-bg: hsl(227, 47%, 96%);
  --profile-text-color: hsl(228, 12%, 44%);
  --main-text: hsl(230, 17%, 14%);
  --toggle: hsl(230, 22%, 74%);
}

body {
  font-family: "Inter", sans-serif;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
}

body.dark header.title {
  font-size: 14px;
}
body.dark header.title h1 {
  font-size: 1.5rem;
  align-self: flex-start;
}
body.dark header.title h1 span {
  display: block;
  color: var(--profile-text-color);
  font-size: 1rem;
  margin-top: 0.2em;
}
@media (min-width: 40em) {
  body.dark header.title h1 {
    font-size: 2rem;
  }
  body.dark header.title h1 span {
    display: block;
    color: var(--profile-text-color);
    font-size: 1rem;
    margin-top: 0.2em;
  }
}

body.dark section.top {
  text-align: center;
}
body.dark section.top .card-grid .profile {
  color: var(--profile-text-color);
  font-size: 0.75rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
body.dark section.top .card-grid h2 {
  font-size: 3.25rem;
  margin-bottom: 1.5rem;
}
body.dark section.top .card-grid h2 span {
  display: block;
  color: var(--profile-text-color);
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
}
body.dark section.top .card-grid .indicator-up, body.dark section.top .card-grid .indicator-down {
  font-weight: 700;
}
body.dark section.top .card-grid .indicator-up img, body.dark section.top .card-grid .indicator-down img {
  margin-right: 0.3125rem;
}
body.dark section.top .card-grid .indicator-up {
  color: var(--up-indicator);
}
body.dark section.top .card-grid .indicator-down {
  color: var(--down-indicator);
}

body.dark section.bottom h3 {
  font-size: 1.5rem;
}
body.dark section.bottom .card-grid .indicator-up, body.dark section.bottom .card-grid .indicator-down {
  font-weight: 700;
}
body.dark section.bottom .card-grid .indicator-up img, body.dark section.bottom .card-grid .indicator-down img {
  margin-right: 0.3125rem;
}
body.dark section.bottom .card-grid .indicator-up {
  color: var(--up-indicator);
}
body.dark section.bottom .card-grid .indicator-down {
  color: var(--down-indicator);
}
body.dark section.bottom .card-grid .info {
  color: var(--profile-text-color);
  font-size: 0.875rem;
  font-weight: 700;
}
body.dark section.bottom .card-grid .info__number h4 {
  color: var(--main-text);
  font-size: 2rem;
}
body.dark section.bottom .card-grid .info__number span {
  font-size: 0.875rem;
  font-weight: 700;
}

body.light .title .toggle::before {
  color: var(--profile-text-color);
}
body.light section.bottom h3 {
  color: var(--profile-text-color);
}

body.dark .top-bg {
  position: absolute;
  top: 0;
  width: 100%;
  max-width: 100%;
  min-height: 30vh;
  background: var(--top-main-bg);
  border-radius: 0 0 1.25rem 1.25rem;
  z-index: -1;
}
body.dark .title {
  position: relative;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  width: 100%;
  max-width: 85%;
  margin: 0 auto;
  padding: 2rem 0 1.5rem;
}
@media (min-width: 40em) {
  body.dark .title {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: none;
  }
}

body.light .title {
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
@media (min-width: 40em) {
  body.light .title {
    border-bottom: none;
  }
}

body section.top, body section.bottom {
  width: 100%;
  max-width: 85%;
  margin: 0 auto;
}
body section.top {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
  margin-top: 1rem;
}
body section.bottom {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
  margin: 3rem auto;
}
@media (min-width: 40em) {
  body section.top {
    grid-template-columns: repeat(2, 1fr);
  }
  body section.bottom {
    grid-template-columns: repeat(2, 1fr);
  }
  body section.bottom h3 {
    grid-column: 1/3;
  }
}
@media (min-width: 71.875em) {
  body section.top {
    grid-template-columns: repeat(4, 1fr);
  }
  body section.bottom {
    grid-template-columns: repeat(4, 1fr);
  }
  body section.bottom h3 {
    grid-column: 1/5;
  }
}

body.dark section.top .card-grid {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: var(--card-bg);
  border-radius: 0.3125rem;
  padding: 2rem 0 1.5rem;
  overflow: hidden;
}
body.dark section.top .card-grid .profile img {
  margin-right: 0.4375rem;
}
body.dark section.top .card-grid .profile, body.dark section.top .card-grid .indicator-up, body.dark section.top .card-grid .indicator-down {
  display: flex;
  align-items: center;
}
body.dark section.top .card-grid__facebook {
  border-top: 4px solid var(--facebook-color);
}
body.dark section.top .card-grid__twitter {
  border-top: 4px solid var(--twitter-color);
}
body.dark section.top .card-grid__instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(225deg, var(--instagram-start-color), var(--instagram-end-color));
}
body.dark section.top .card-grid__youtube {
  border-top: 4px solid var(--youtube-color);
}

body.dark section.bottom .card-grid {
  position: relative;
  display: flex;
  flex-direction: column;
  background: var(--card-bg);
  border-radius: 0.3125rem;
  padding: 1.5rem;
  overflow: hidden;
}
body.dark section.bottom .card-grid .indicator-up, body.dark section.bottom .card-grid .indicator-down {
  display: flex;
  align-items: center;
}
body.dark section.bottom .card-grid .info {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
body.dark section.bottom .card-grid .info__number {
  margin-top: 1.5rem;
}

body.dark section .card-grid {
  transition: 350ms;
}
body.dark section .card-grid:hover {
  background: #343b56;
  cursor: pointer;
}

body.light section .card-grid:hover {
  background: #e0e4f0;
}

body.dark {
  background: var(--main-bg);
  color: var(--main-text);
}
body.dark .container-toggle {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 85%;
  margin: 1.5rem auto;
}
body.dark .container-toggle span.indicator {
  user-select: none;
  font-weight: 700;
  color: var(--profile-text-color);
  transition: 350ms;
}
body.dark .container-toggle span.d-none {
  display: none;
}
body.dark .container-toggle div.light-mode span {
  transform: translateX(150%);
}
body.dark .container-toggle .toggle {
  position: relative;
  padding: 0.2rem;
  width: 60px;
  border-radius: 80px;
  background: var(--toggle);
  cursor: pointer;
}
body.dark .container-toggle .toggle span {
  display: block;
  width: 40%;
  aspect-ratio: 1/1;
  background: var(--top-main-bg);
  border-radius: 50%;
  transition: 350ms;
}
@media (min-width: 40em) {
  body.dark .container-toggle {
    display: none;
  }
  body.dark .title .toggle {
    position: relative;
    padding: 0.2rem;
    width: 60px;
    border-radius: 80px;
    background: var(--toggle);
    cursor: pointer;
  }
  body.dark .title .toggle::before {
    content: "Dark Mode";
    position: absolute;
    left: -150%;
    top: 50%;
    transform: translateY(-50%);
    color: var(--profile-text-color);
    font-weight: 700;
    transition: 350ms;
  }
  body.dark .title .toggle span {
    display: block;
    width: 40%;
    aspect-ratio: 1/1;
    background: var(--top-main-bg);
    border-radius: 50%;
    transition: 350ms;
  }
}

@media (min-width: 40em) {
  body.light .title .toggle::before {
    content: "Light Mode";
    opacity: 1;
  }
  body.light .title .toggle span.light-mode {
    transform: translateX(150%);
  }
}